export default {
  props: ['keyword'],
  data () {
    return {
      inputVal: this.keyword
    };
  },
  methods: {
    // 清空输入框
    clearInput () {
      this.inputVal = ''
    },
    confirmHandler () {
      // 如果inputVal不为空，触发父组件搜索
      if (this.inputVal) {
        // 给父组件传一个search事件，然后传当前的输入框的值
        this.$emit('search', this.inputVal)
      }
    }
  },
  watch: {
    keyword (newValue) {
      this.inputVal = newValue
    }
  }
}
